import React from "react"
import Layout from "../components/Layout"
import styles from "../styles/project-details.module.css"
import { graphql } from "gatsby"

export default function ProjectDetails({ data }) {
  const { html } = data.markdownRemark
  const { title, stack } = data.markdownRemark.frontmatter
  
  return (
    <div>
      <Layout>
		  <div className="">
			<div className="container mx-auto px-20 pt-28 pb-24">
			  <div className="text-3xl font-semibold">{title}</div>
			  <div className="text-lg font-bold">{stack}</div>
			  <div className="py-8" dangerouslySetInnerHTML={{ __html: html }} />
			</div>
		  </div>
      </Layout>
    </div>
  )
}

export const query = graphql`
  query ProjectsDetails ($slug: String) {
    markdownRemark(frontmatter: {slug: {eq: $slug}}) {
      html
      frontmatter {
        stack
        title
      }
    }
  }    
`
